/* src/Calendar.css */

.container {
    max-width: 1200px;
    margin: auto;
  }
  
  body {
    font-family: 'Roboto Mono', sans-serif;
  }  
  
  .card-header {
    background-color: #343a40;
    color: white;
  }
  
  .card-body {
    background-color: #f8f9fa;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    padding: 5px 0;
    display: flex;
    align-items: center;
  }
  
  img {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }